@use '../../unstable__bpk-mixins/typography';

.bpk-list {
  @include typography.bpk-list;

  &__item {
    @include typography.bpk-list-item;
  }

  & & {
    @include typography.bpk-list--nested;

    /* stylelint-disable selector-max-compound-selectors */

    > li {
      margin-bottom: 0;
    }
  }
}
