@use '../../unstable__bpk-mixins/tokens';
@use '../../unstable__bpk-mixins/utils';

.bpk-calendar-grid {
  border-collapse: separate;
  border-spacing: 0;

  &--animating {
    z-index: 1;
    transition: transform 0.2s ease-in-out;
  }

  &__caption {
    @include utils.bpk-visually-hidden;
  }
}
