@use '../../unstable__bpk-mixins/tokens';
@use '../../unstable__bpk-mixins/borders';

.bpk-horizontal-nav {
  position: relative;

  &--show-default-underline {
    @include borders.bpk-border-bottom-sm(tokens.$bpk-line-day, '');
  }

  &--show-light-underline {
    @include borders.bpk-border-bottom-sm(tokens.$bpk-line-day, '');
  }

  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    align-items: stretch;
    list-style-type: none;
  }
}
