@use '../../unstable__bpk-mixins/cards';

.bpk-card {
  @include cards.bpk-card;

  &--atomic-button {
    width: 100%;
    padding: 0;
    border: none;
    background: none;
    color: inherit;
    text-align: inherit;
    cursor: pointer;
    appearance: none;
  }

  &--padded {
    @include cards.bpk-card--padded;
  }
}
