@use '../../packages/unstable__bpk-mixins/tokens';

.bpk-card-examples {
  &__header {
    display: flex;
    height: tokens.bpk-spacing-xxl();
    padding: tokens.bpk-spacing-md();
    justify-content: space-between;
    align-items: center;
    color: tokens.$bpk-text-on-dark-day;
  }

  &__wrapper {
    width: tokens.bpk-spacing-md() * 41;
  }
}
