@use '../../packages/unstable__bpk-mixins/tokens';

.bpk-rating-story {
  &--img {
    height: tokens.bpk-spacing-base();
    margin-right: -1 * (tokens.$bpk-one-pixel-rem * 6);
    margin-bottom: tokens.$bpk-one-pixel-rem;
    margin-left: -1 * (tokens.$bpk-one-pixel-rem * 6);
    vertical-align: middle;
  }

  &--trip {
    height: tokens.bpk-spacing-base();
    vertical-align: middle;
  }
}
