@use '../../unstable__bpk-mixins/tokens';
@use '../../unstable__bpk-mixins/radii';
@use 'variables';

.bpk-calendar {
  display: flex;
  width: 100%;
  flex-direction: column;

  @include radii.bpk-border-radius-sm;

  &--fixed {
    width: variables.$calendar-width;
  }

  &__header {
    margin-top: tokens.bpk-spacing-lg();
  }

  &__grid {
    margin-top: tokens.bpk-spacing-lg();
  }
}
