@use '../../unstable__bpk-mixins/tokens';
@use '../../unstable__bpk-mixins/typography';

.bpk-chart {
  &__axis-tick {
    &-text {
      fill: tokens.$bpk-text-secondary-day;

      @include typography.bpk-text;
      @include typography.bpk-caption;
    }
  }

  &__axis-label {
    fill: tokens.$bpk-text-secondary-day;

    @include typography.bpk-text;
    @include typography.bpk-caption;
  }
}
