@use '../../unstable__bpk-mixins/tokens';
@use 'variables';

.bpk-calendar-grid-transition {
  position: relative;
  overflow: hidden;

  &__dummy {
    display: inline-block;
    width: variables.$calendar-width;
  }

  &__strip {
    left: 0;
    display: block;
    z-index: 0;
    width: 3 * variables.$calendar-width;

    &--transitioning {
      z-index: 1;
      transition: transform tokens.$bpk-duration-sm ease-in-out;
    }
  }

  &__grid {
    display: inline-table;
    width: variables.$calendar-width;
  }
}
