@use '../../unstable__bpk-mixins/tokens';

.bpk-input-field {
  display: flex;
  max-width: 3 * tokens.bpk-spacing-xxl();
  margin-right: tokens.bpk-spacing-sm();
  align-items: center;

  input {
    text-align: center;
  }
}

.bpk-input-field:last-child {
  margin-right: tokens.$bpk-spacing-none;
}
