@use '../../packages/unstable__bpk-mixins/tokens';
@use '../../packages/unstable__bpk-mixins/breakpoints';

.bpk-forms {
  &__viewport-alert {
    display: none;

    @include breakpoints.bpk-breakpoint-tablet {
      display: block;
    }
  }

  &__form {
    margin-bottom: tokens.bpk-spacing-md();

    &--desktop-only {
      @include breakpoints.bpk-breakpoint-tablet {
        display: none;
      }
    }
  }

  &__place {
    display: inline-block;
    width: 32%;
    vertical-align: bottom;
  }

  &__date {
    display: inline-block;
    width: 18%;
    vertical-align: bottom;
  }

  &__hotels-destination {
    display: inline-block;
    width: 48%;
    vertical-align: bottom;
  }
}
